

































import Loading from "@/components/Loading/index.vue";

import { mapState } from "vuex";
import Vue from "vue";
export default Vue.extend({
  name: "New_List",
  components: {
    Loading,
  },
  props: {
    musicList: {
      type: Array,
      default: () => [],
    },
    showNum: {
      type: Boolean,
      default: () => false,
    },
    loading: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {};
  },
  computed: {
    ...mapState({}),
  },
  created() {
    console.log(this.musicList);
  },
  filters: {
    num(index) {
      if (index < 10) {
        return "0" + index;
      }
    },
  },
  methods: {
    goToPlay(id) {
      this.$router.push({
        name: "PlaySong",
        params: {
          songId: id,
        },
      });
    },
  },
});
